import React from 'react';
import { Box, Typography, styled } from '@mui/material';
import UserInfoCard from '../../components/Molecules/UserInfoCard';
import { useMetaMask } from '../../hooks/MetamaskProvider/MetamaskProvider';
import { Button } from '../../components/Atoms/Button';
import ConnectButton from '../../components/Molecules/ConnectButton';

const Home = () => {
  const { wallet, hasProvider, isConnecting, connectMetaMask } = useMetaMask();
  console.log('wallet', wallet);
  console.log('hasProvider', hasProvider);
  console.log('isConnecting', isConnecting);

  return (
    <HomePageContainer>
      <MainWrapper>
        {window.ethereum?.isMetaMask && wallet.accounts.length < 1 && (
          <ConnectButton
            text={'Connect Your Wallet'}
            isConnecting={isConnecting}
            connectMetaMask={connectMetaMask}
          />
        )}
        {hasProvider && wallet.accounts.length > 0 && (
          <UserInfoCard info={wallet} />
        )}
      </MainWrapper>
    </HomePageContainer>
  );
};

const HomePageContainer = styled(Box)(() => ({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'flex-end',
  alignItems: 'center',
  width: '100vw',
  height: 'calc(100vh - 100px)',
}));

const MainWrapper = styled(Box)(() => ({
  display: 'flex',
  boxSizing: 'border-box',
  justifyContent: 'center',
  alignItems: 'center',
  width: '100%',
  height: 'calc(100% - 50px)',
  background: '#191919',
}));

export default Home;
